import {React, useState, useEffect} from 'react'
import './Stats.css'

function Stats() {
    const [github,setGithub] = useState([])
    const [repos,setRepos] = useState([])

    useEffect(() => {
        fetch('https://api.github.com/users/iampranavdhar')
            .then(response => response.json())
            .then(result => setGithub(result));
        fetch('https://api.github.com/users/iampranavdhar/repos')
            .then(response => response.json())
            .then(repos => setRepos(repos))
    },[])

    const totalStars = () =>{
        const each_repo_stars = repos.map(({stargazers_count}) => stargazers_count) //For getting the list of all the stars for each repo
        const total = each_repo_stars.reduce((a, b) => a + b, 0) //For finding sum of the elements in a list
        return total
    }

    return (
        <div className='stats' id='stats'>
            <h1 className='stats-title'>Stats</h1>
            <div className='stats-blocks'>
                <div className="github-followers">
                    <div>
                        <p className='stats-heading'>GitHub Followers</p>
                        <p className='stats-count'>{github.followers}</p>
                    </div>
                    <div>
                        <img className='stats-pic' alt='' src="data:image/svg+xml;base64," />
                    </div>
                    
                </div>
                <div className="github-stars">
                    <div>
                        <p className='stats-heading'>GitHub Stars <span ></span></p>
                        <p className='stats-count'>{totalStars()}</p>
                    </div>
                    <div>
                        <img className='stats-pic' alt='' src="data:image/svg+xml;base64," />
                    </div>
                </div>
                <div className="github-repos">
                    <div>
                        <p className='stats-heading'>GitHub Repos</p>
                        <p className='stats-count'>{repos.length}</p>
                    </div>
                    <div>
                        <img className='stats-pic' alt='' src="data:image/svg+xml;base64," />
                    </div>
                </div>
                <div className="page-views">
                    <div>
                        <p className='stats-heading'>No.of Visitors</p>
                        <p className='stats-count'>NA</p>
                    </div>
                    <div>
                        <img className='stats-pic' alt='' src="data:image/svg+xml;base64," />                    </div>
                </div>
            </div>
        </div>
    )
}

export default Stats